/*- Custom style -*/

// -- Put custom style under this point -- //

// No img bottom

table img {
  margin-bottom: 0;
}

table {
  thead th {
    position: sticky;
    background-color: #ccc;
    color: #666666;
    top: 0;
    z-index: 1;
  }

  td.actions {
    vertical-align: middle;
    width: 25px;
  }

  td.centered {
    text-align: center;
  }

  td.small {
    font-size: 0.8em;
    white-space: nowrap;
  }
}

// https://www.w3schools.com/css/css_tooltip.asp
/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
  /* If you want dots under the hoverable text */
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;

  width: 120px;
  bottom: 100%;
  left: 50%;
  margin-left: -60px;
  /* Use half of the width (120/2 = 60), to center the tooltip */
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 100%;
  /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

// Table Hover

@media only screen and (max-width: 480px) {
  .table-wrapper {
    width: 100%;
    overflow-x: auto;
  }
}

table {
  border-collapse: collapse;
  width: 100%;

  a {
    text-decoration: none;
  }
}

tbody tr:hover {
  background-color: #eee;
}

// Sortable tables

table.sortable thead {
  font-weight: bold;
  cursor: pointer;
}

// Details

details {
  summary {
    font-size: 1.2rem;
    cursor: pointer;

    p {
      display: inline;
    }
  }
}

// Sharing Links

.sharing-links {
  text-align: right;
  font-size: 0.9em;
  margin-top: 6px;

  a {
    font-weight: normal;
  }
}

// Banner

.page-banner {
  // width: 100%;
  background-color: #159957;
  color: #fff;
  text-align: center;
  padding: 2px 0;
  font-size: 0.8em;
  letter-spacing: 0.025em;

  a {
    color: #fff;
    font-weight: normal;
    // text-decoration: none;
  }
}
